import { createContext, useEffect, useState } from "react";
import supabase from "../supabase";

export const AttivitaContext = createContext();

export const AttivitaProvider = ({ children }) => {
    const [attivit, setAttivit] = useState(null);
    const [loadingAttivit, setLoadingAttivit] = useState(true);

    async function fetchAttivit() {
        let { data: attivit, error } = await supabase
            .from('ATTIVITA')
            .select('*')
            .eq('id', 24)
            .single();

        if (error) {
            console.error('Errore nella query:', error);
            setAttivit(null);
        } else {
            console.log("Attivit:", attivit);
            setAttivit(attivit);
        }
        setLoadingAttivit(false);
    }

    useEffect(() => {
        fetchAttivit();
    }, []);

    return (
        <AttivitaContext.Provider value={{ attivit, loadingAttivit }}>
            {children}
        </AttivitaContext.Provider>
    );
};
